import * as React from 'react';
import {
  RenderToolbar,
  ItemKey,
  ItemSlot,
  SlotType,
} from '@ali/4ever-component';
import { ITranslateConfig } from './types';
import createTranslateButton from './components/next/translateButton';
import NewTranslationButton from './components/next/newTranslateButton';

export default function createRenderToolbar(
  config: ITranslateConfig,
): RenderToolbar {
  return (props, controller) => {
    const { buttonName, extraProps } = props;

    if (buttonName === ItemKey.translate) {
      if (!config.enableDocTranslation) {
        return createTranslateButton({
          controller,
          config,
          locale: config.locale,
          ...extraProps,
        });
      }
      return (
        <ItemSlot display={SlotType.iconMenu}>
          <NewTranslationButton
            controller={controller}
            config={config}
            {...extraProps}
          />
        </ItemSlot>
      );
    }

    return null;
  };
}
